
import React, { useState, useEffect } from 'react';

const Filter = (props) => {
  const[index,setindex] = useState("")
    const filtercontent = props.data
    const updateid = (id) =>
    {
      setindex(id)
    }
    return (
    <div className = "filterpart">
    <span class="filter"><i class="fas fa-filter"></i><span className="filtertext">Filter By Category</span></span>
 <div className = "filterspace addcolor"> 
      {filtercontent.map((item) =>
       (
         <button key={item.id}className =  {index == item.id ? "filterbtn add-color" : "filterbtn"} 
         onClick = {() =>
          {updateid(item.id)
         props.addproduct(item.slug)}
          }>{item.title}</button>
      ))}
    </div>

  </div>);
}
 
export default Filter;