import './App.css';
import {filterdata, blogsdata} from "./components/filterdata";
import React, { useState, useEffect } from 'react';
import Topbar from "./components/topbar";
import Filter from "./components/filterpart";
import Blogpart from"./components/blogspart";
import { useParams } from 'react-router';
import {BrowserRouter as Router,Switch,Link,Route,Redirect} from "react-router-dom";



function App() {


 


const Home = () => {
  return ( <h1>Home page</h1> );
}

const About = () => {
  return ( <h1>About page</h1> );
}
 
const Contact = () => {
  return ( <h1>contact page</h1> );
}

const Users = () => {
  const {id} = useParams()
  return (<h1>Users : {id}</h1>  );
}
 
class Auto extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect : false
      }
     
  }
componentDidMount()
  {
  setTimeout(this.setState({redirect:true}),3000)
  }
 
  render() { 
    if(this.state.redirect)
    {
     return <Redirect to="/contact"/>
    }
    return ( 
    <h1><Link to="/test">test page</Link></h1>
     );
  }
}
 


 








 const filtercontent = filterdata
 const [blogsData,setblogsData] = useState([])
 const[searchvalue,setsearchvalue] = useState("")
 useEffect(()=>{
  setblogsData(blogsdata)
 },[])
const onsearch = ()=>
{var result = blogsData.filter(item => item.authorname.includes(searchvalue))
  // .filter((item) => (item.authorname).includes(searchvalue))
  console.log(result)
  setblogsData(result)
}

const checkslug = (slug) =>
{
  let updateblog = blogsData.filter((item) => item.slug === slug)
  setblogsData(updateblog)
}
  return (

    <div className="App">




<Router>
<ol>
  <li><Link to = "/home">Home</Link></li>
  <li><Link to = "/contact">contact</Link></li>
  <li><Link to = "/about">about</Link></li>
  <li><Link to="/test">Test</Link></li>
  
</ol>
<Switch>
  <Route path = "/home" component = {Home}/>
  <Route path = "/contact" component = {Contact}/>
  <Route path = "/about" component = {About}/>
  <Route path="/users/:id" component = {Users}/>
  <Route path="/test" component =  {Auto}/>
  <Route component = {()=><h1>404 Page not found error</h1>}/>
</Switch>

      </Router>
   








        <Topbar/>
       

        <Filter data = {filtercontent} addproduct = { (slug) =>
        {
          checkslug(slug)
        }}/>
      <input onChange = {(e) =>setsearchvalue(e.target.value)}/> <button onClick = {onsearch}>search</button> 
        <Blogpart data = {blogsData}/>
       
</div>
  );
}

export default App;